.aluguel-container {
  text-align: center;
  padding: 2rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 colunas para telas grandes */
  gap: 1.5rem;
  max-width: 1200px;
  margin: 0 auto;
}

.aluguel-card {
  background-color: #a573ff;
  color: #fff;
  border-radius: 8px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Alinhamento vertical */
  text-align: center;
  height: 300px;
}

p {
  font-size: 1.3rem;
}

.aluguel-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.aluguel-buttons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 2rem;
}

.button {
  background-color: #6c00ff;
  color: #fff;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #4900b8;
}

/* Responsividade para telas médias (tablets) */
@media (max-width: 992px) {
  .card-grid {
    grid-template-columns: repeat(2, 1fr); /* 2 colunas em tablets */
  }
}

/* Responsividade para telas pequenas (celulares) */
@media (max-width: 600px) {
  .card-grid {
    grid-template-columns: 1fr; /* 1 coluna em dispositivos móveis */
  }

  .aluguel-card {
    height: auto; /* Ajuste de altura para que o conteúdo se ajuste */
    padding: 2rem 1rem; /* Ajuste de padding para uma melhor aparência */
  }
}